/* 기본 설정 */
body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #121212; /* 검정 배경 */
    color: #f0f0f0; /* 밝은 텍스트 */
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

/* 헤더, 섹션, 푸터 스타일 */
header {
    background-color: #1e1e1e; /* 검정에 가까운 배경 */
    color: #0073e6; /* 파란 텍스트 */
    padding: 2rem;
    text-align: center;
    width: 100%;
}

section {
    background-color: #1e1e1e; /* 섹션마다 검정 배경 */
    padding: 2rem;
    border-radius: 10px;
    margin: 2rem 0;
    width: 80%;
}

footer {
    background-color: #333;
    color: white;
    padding: 1rem;
    text-align: center;
    width: 100%;
    position: fixed;
    bottom: 0;
}

h1, h2 {
    margin: 0;
    color: #0073e6; /* 파란 텍스트 */
}

a {
    color: #0073e6; /* 파란 링크 */
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

/* 버튼 스타일 */
button {
    background-color: #0073e6; /* 파란 배경 */
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #005bb5; /* 호버 시 더 진한 파랑 */
}

/* 반응형 레이아웃 */
@media (max-width: 768px) {
    section {
        padding: 1rem;
        width: 90%;
    }

    header {
        padding: 1rem;
    }

    footer {
        padding: 1rem;
    }
}

